import React from 'react';
import axios from 'axios';
import Movie from './Movie';
import "./App.css";
//import PropTypes from 'prop-types'; // prop의 값이 전달하려는 곳과 받는 곳이 같은지 확인 해주는 기능 인스톨 후 사용

class App extends React.Component { //react는 나의 class component의 render method를 실행한다.


    state = {
      isLoading: true,
      movies: []
    };
    getMovies = async () => { // axios가 끝날때까지 기다렸다가 실행해! (비동기) 공부해야함.
      const {
        data: {
          data: {
            movies
          }
        }
      } = await axios.get("https://yts-proxy.now.sh/list_movies.json?sort_by=rating");
      this.setState({
        movies,
        isLoading: false
      })
    };
    componentDidMount() {
      this.getMovies();
    }

    render() { //온클릭에서 펑션을 호출할때 ()를 쓰지 않는다.
        const {isLoading, movies} = this.state;
        return ( <section className = "container" > 
        {isLoading ?
        <div className = "loader">
          <span className ="loader__text">
            Loading...
          </span>
        </div>
         : <div className = "movies">
           {movies.map(movie => {
                  console.log(movie);
                  return (<Movie
                    id = {movie.id}
                    year = {movie.year}
                    title = {movie.title}
                    summary = {movie.summary}
                    poster = {movie.medium_cover_image}
                    genres = {movie.genres}
                    />);})}</div>}
              </section>
                );
              }
            }

            export default App;